.user-list-container {
  margin: 0 auto;
  padding: 20px;
  width: 100%;
  max-width: 100vw;
  border-radius: 8px;
  border: 1px solid var(--colorTextOrange);
  box-shadow: 2px 2px 5px rgba(0, 0, 0, 1),
    -2px -2px 5px rgba(255, 255, 255, 0.14);
  margin-bottom: 100vh;

  h2 {
    text-align: center;
    font-size: 28px;
    margin-bottom: 20px;
    color: var(--colorTextOrange);
  }

  /* Контейнер для карток користувачів */
  .user-cards {
    display: grid;
    grid-template-columns: 1fr;
    gap: 20px;
    // width: 100%;
    // background: #000;
  }

  .user-card {
    padding: 20px;
    border-radius: 8px;
    border: 1px solid rgba(175, 130, 53, 0.513);
    box-shadow: 2px 2px 5px rgba(0, 0, 0, 1),
      -2px -2px 5px rgba(255, 255, 255, 0.14);
    transition: all 0.3s;

    .formEditUser {
      display: flex;
      flex-direction: column;
      justify-content: center;
      border-radius: 10px;
      // width: 100%;
      width: 350px;
      color: var(--colorTextOrangeHover);
      background-color: transparent;
    }

    &:hover {
      box-shadow: 0 6px 18px rgba(175, 110, 53, 0.513);
    }
  }

  .user-card p {
    margin: 5px 0;
    color: var(--colorTextOrangeHover);
  }

  .input-email-edit-user[type="email"] {
    width: 94%;
    border: 1px solid #ccc;
    border-radius: 5px;
  }

  .buttons-container {
    display: flex;
    justify-content: space-between;
    flex-direction: column;
    flex-wrap: wrap;
  }

  .checkbox-container {
    margin: 20px 10px;
    .checkbox {
      margin: 0 10px;
    }
  }

  .success-message {
    background-color: #d4edda;
    color: #155724;
    text-align: center;
    padding: 10px;
    border-radius: 4px;
    margin-bottom: 20px;
  }

  .error-message {
    background-color: #f8d7da;
    color: #721c24;
    text-align: center;
    padding: 10px;
    border-radius: 4px;
    margin-bottom: 20px;
  }
}
